body {
  background-color: #f1faee;
  min-height:100vh;
  position: relative;
}

.App {
  position: relative;
  min-height:100vh;
}

a {
  position: relative;
  text-decoration: none;
}

a:hover::after, a:focus::after {
  opacity: 1;
  transform: translateY(0px);
}

a::after {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 1px;
  background: #1d3557;
  content: '';
  opacity: 0;
  transition: opacity 0.2s, transform 0.2s;
  transform: translateY(4px);
}

p {
  color: #1d3557;
}

h2 {
  color: #1d3557;
}

hr.menubar {
  border: 3px solid #f1faee;
  border-radius: 3px;
  margin-top: 40px;
  margin-bottom:40px;
  width:800px;
}

.vertical-bar {
  display:inline;
  border-left: solid #f1faee;
  border-radius: 3px
}

.banner {
  display:flex;
}

.revealComp-1 {
  font-size: 100px;
  font-family:"Gotham";
  font-weight: bold;
  color:#457b9d;
}

.revealComp-2 {
  font-size: 100px;
  font-family:"Gotham";
  font-weight: bold;
  color:#e63946;
} 

.left-col {
  padding: 100px 50px;
  display:block;
  flex:1.5;
}

.right-col {
  font-family: Gotham;
  display:block;
  flex: 1;
  font-size: 20px;
  padding: 100px 0px;
  margin-left: 0px;
  animation: text 3s 1;
}

.right-col p {
  font-size: 20px;
  margin-left:0px;
  width:70%;
  line-height: 1.5em;
}

.right-col a {
  margin-left:0px;
  animation: text 3s 1;
}

.banner-1 {
  margin-bottom: 100px;
  display:flex;
}

.left-col-1 {
  font-family: Gotham;
  display:block;
  padding: 80px 0px;
  flex: 1;
  font-size: 20px;
  animation: text 3s 1;
}

.left-col-1 p {
  font-size: 20px;
  margin-left:150px;
  width:70%;
  line-height: 1.5em;
}

.left-col-1 a {
  margin-left:150px;
}

.right-col-1 img {
  width:60%;
}

.right-col-1 {
  padding: 100px 50px;
  display:block;
  flex:1;
  text-align:center;
}

.banner-2 {
  display:flex;
  margin-bottom: 100px;
}

.left-col-2 {
  flex:1;
  display:block;
  text-align:center;
}

.left-col-2 img {
  padding: 120px;
  width:60%;
}

.right-col-2 {
  font-family: Gotham;
  display:block;
  padding: 80px 0px;
  flex: 1;
  font-size: 20px;
  animation: text 3s 1;
}

.right-col-2 h2 {
  margin-left: 100px;
}

.right-col-2 p {
  font-size: 20px;
  margin-left:100px;
  width:70%;
  line-height: 1.5em;
}

.right-col-2 a {
  margin-left:150px;
}


.footer {
  bottom:0;
  width:100%;
  display:flex;
  height:300px;
  background:#1d3557;
  color: #f1faee;
  font-family:Gotham;
  list-style:none;
  line-height: 2em;
}

.footer-left {
  padding: 50px;
  flex:1;
}

.footer-left ul {
  list-style:none;
  line-height: 2em;
}

.footer-right ul {
  list-style:none;
  line-height: 1.8em;
}

.footer-right {
  background:#1d3557;
  padding:50px;
  flex:1;
}

.footer-right a {
  color: #f1faee;
}

.footer-bottom {
  display:block;
}

@keyframes text {
  0%{
    color:#f1faee;
    margin-left: -20px;
}
}






